import React, {
  useContext,
  useMemo,
  useState,
  useEffect,
} from "react";
import {
  Brightness4,
  Brightness7,
} from "@material-ui/icons";

import { UserContext } from "../../context/UserContext";

import { Link } from "react-router-dom";
import { useFetchData } from "../../hooks/useFetchData";
import Storage from "../../serivce/localStorage";

export default function Userbox() {
  const [theme, setTheme] = useState(false);
  const { user: currentUser } = useContext(UserContext);

  const url = `users/${currentUser?._id}`;
  const [user] = useFetchData(url);
  useEffect(() => {
    if (Storage.get("theme")) {
      document.documentElement.classList.remove("dark");
    } else {
      document.documentElement.classList.add("dark");
    }
  });
  return useMemo(() => {
    const handlerClickLight = () => {
      setTheme(false);
      Storage.set("theme", theme);
      document.documentElement.classList.add("dark");
    };
    const handlerClickDark = () => {
      setTheme(true);
      Storage.set("theme", theme);
      document.documentElement.classList.remove("dark");
    };

    return (
      <div className='bg-white p-4 text-gray-600 flex flex-col space-y-6 shadow-lg rounded-md dark:bg-gray-800 dark:text-gray-200'>
        <div className='flex justify-between items-center'>
          <Link
            to={user ? `/profile/${user.username}` : "/"}
          >
            <img
              src={
                user?.profilePicture ||
                "https://fujix.oss-cn-shenzhen.aliyuncs.com/noCover.png"
              }
              className='w-16 h-16 object-cover rounded-md'
              alt='avatart'
            />
          </Link>

          <div>{user?.username}</div>
          {theme ? (
            <span
              className='cursor-pointer'
              onClick={handlerClickLight}
            >
              <Brightness7 />
            </span>
          ) : (
            <span
              className='cursor-pointer'
              onClick={handlerClickDark}
            >
              <Brightness4 />
            </span>
          )}
        </div>
        <Link
          to='/home'
          className='text-center hover:text-pink-400'
        >
          分享一刻
        </Link>
        <Link
          to='/home/photos'
          className='text-center  hover:text-pink-400'
        >
          照片流
        </Link>
        <Link
          to='/home/timeline'
          className='text-center hover:text-pink-400'
        >
          时刻岛
        </Link>
      </div>
    );
  }, [user, theme]);
}
