import { useState, useEffect } from "react";
import http from "../../serivce/API";

import { useContext } from "react";
import {
  RssFeed,
  PlayCircleFilled,
  Group,
} from "@material-ui/icons";

import { Link } from "react-router-dom";
import { UserContext } from "../../context/UserContext";

import Share from "../share/Share";
import Post from "../post/Post";
import { useLocation } from "react-router-dom";

export default function Feed({ username }) {
  const [posts, setPosts] = useState([]);
  const { user } = useContext(UserContext);

  const params = useLocation();
  const SearchKey = params.search.split("=").pop();

  useEffect(() => {
    const fetchPosts = async () => {
      if (SearchKey) {
        const res = await http.get(
          `posts/friends/${SearchKey}`
        );
        setPosts(
          res.data?.flat().sort((p1, p2) => {
            return (
              new Date(p2.createdAt) -
              new Date(p1.createdAt)
            );
          })
        );
      } else {
        const res = username
          ? await http.get("posts/profile/" + username)
          : await http.get(`posts/timeline/now`);

        setPosts(
          res.data?.sort((p1, p2) => {
            return (
              new Date(p2.createdAt) -
              new Date(p1.createdAt)
            );
          })
        );
      }
    };
    fetchPosts();
  }, [username, SearchKey]);

  return (
    <div className='w-full'>
      {!username && (
        <div className='hidden lg:block w-56 bg-white text-gray-600 rounded-md p-4 space-y-4 shadow-lg fixed dark:bg-gray-800 dark:text-gray-200 '>
          <h3 className=''>首页</h3>
          <div className='flex flex-col space-y-4'>
            <Link to='/home/share'>
              <RssFeed />
              <span className='pl-4'>全部关注</span>
            </Link>

            <Link to='/'>
              <PlayCircleFilled />
              <span className='pl-4'>特别关注</span>
            </Link>

            <Link to={`?userId=${user?._id}`}>
              <Group />
              <span className='pl-4'>好友圈</span>
            </Link>
          </div>
        </div>
      )}

      <div className='w-full  lg:w-9/12  ml-auto space-y-8 '>
        <Share username={username} />
        {posts.map((p) => {
          return <Post key={p._id} post={p}></Post>;
        })}
      </div>
    </div>
  );
}
