import { useState, useEffect } from "react";
import http from "../serivce/API";

export const useFetchPosts = (url) => {
  const [posts, setPosts] = useState([]);

  useEffect(() => {
    const fetchPosts = async () => {
      try {
        const res = await http.get(url);

        let filter = res.data?.sort((p1, p2) => {
          return (
            new Date(p2.createdAt) - new Date(p1.createdAt)
          );
        });
        setPosts(
          filter.filter((item) => item.img !== undefined)
        );
      } catch (error) {
        console.log(error);
      }
    };
    fetchPosts();
  }, [url]);

  return posts;
};
