import { useState, useEffect } from "react";
import Feed from "../../components/feed/Feed";
import Rightbar from "../../components/rightbar/Rightbar";
import http from "../../serivce/API";

import { useParams } from "react-router";
import Layout from "../../components/layout/Layout";

export default function Profile() {
  const [user, setUser] = useState({});
  const { username } = useParams();
  useEffect(() => {
    let fetchUser = async () => {
      const res = await http.get(
        `/users?username=${username}`
      );
      setUser(res.data);
    };
    fetchUser();
    return () => {
      fetchUser = null;
    };
  }, [username]);

  return (
    <Layout>
      <div className='bg-white rounded-sm shadow-lg w-full dark:bg-gray-600 overflow-hidden'>
        {/* <Sidebar /> */}

        <div className=''>
          <div className='relative'>
            <img
              src={
                user.coverPicture ||
                "https://fujix.oss-cn-shenzhen.aliyuncs.com/noCover.png"
              }
              alt=''
              className='w-full h-52 object-cover'
            />
            <img
              src={
                user.profilePicture ||
                "https://fujix.oss-cn-shenzhen.aliyuncs.com/noAvatar.png"
              }
              alt=''
              className='w-32 h-32 object-cover rounded-full absolute top-full left-1/2 transform -translate-x-1/2 -translate-y-1/2'
            />
          </div>
          <div className='mt-20 text-center dark:text-gray-200'>
            <div className=''>{user.username}</div>
            <div className=''>{user.desc}</div>
            <button className='bg-white rounded-md shadow-lg p-4 text-center my-4 dark:bg-gray-800 dark:text-gray-200 '>已关注</button>
          </div>
        </div>
        <div className='flex text-gray-600 p-4'>
          <div className='w-9/12'>
            <Feed username={username} />
          </div>
          <div className='w-3/12 ml-4 space-y-8'>
            <Rightbar user={user} />
          </div>
        </div>
      </div>
    </Layout>
  );
}
