import React, { useContext, useEffect } from "react";
import { useRef } from "react";
import { UserContext } from "../../context/UserContext";
import "react-toastify/dist/ReactToastify.css";

import { useHistory } from "react-router-dom";
// import "./login.css";
import API from "../../serivce/API";

import { ToastContainer } from "react-toastify";
import Storage from "../../serivce/localStorage";

export default function Login() {
  const { setUser } = useContext(UserContext);
  const email = useRef();
  const password = useRef();
  const history = useHistory();

  const handleClick = (e) => {
    e?.preventDefault();

    const login = async () => {
      const { data } = await API.post("auth/login", {
        email: email.current.value,
        password: password.current.value,
      });
      // toast.success("🦄 登陆成功");
      Storage.set("token", data.token);
      Storage.set("user", data.user);
      setUser(data.user);
      history.push("/home/share");
    };

    login();
  };

  useEffect(() => {
    const keydownHandler = (e) => {
      if (e.keyCode === 13) {
        handleClick();
      }
    };
    document.addEventListener("keydown", keydownHandler);
    return () => {
      document.removeEventListener(
        "keydown",
        keydownHandler
      );
    };
  });

  const handlerCreate = () => {
    history.push("/register");
  };
  return (
    <div className='mx-auto bg-gradient-to-r from-purple-400 via-pink-500  text-white flex flex-col justify-center space-y-6 h-screen'>
      <ToastContainer
        position='top-center'
        autoClose={2500}
        hideProgressBar={false}
        newestOnTop={false}
        closeOnClick
        rtl={false}
        pauseOnFocusLoss
        draggable
        pauseOnHover
      />
      <div className='flex flex-col h-screen justify-center items-center space-y-10'>
        <div className='w-full  md:w-1/2 flex justify-center items-center flex-col space-y-4 '>
          <h2 className='text-6xl font-bold'>Fujix</h2>
          <span>用照片分享你的生活</span>
        </div>
        <div className='w-full md:w-2/3  lg:w-1/2  flex justify-center items-center '>
          <form
            onSubmit={handleClick}
            className='w-full m-4  md:w-1/2 flex bg-white p-4 flex-col justify-center items-center space-y-4  rounded-md shadow-xl '
          >
            <input
              placeholder='邮箱'
              type='email'
              required
              ref={email}
              value='shaodeyu@gmail.com'
              className='w-full shadow-lg p-2 rounded-md outline-none  border-1 text-gray-600 focus:ring-2 focus:ring-blue-400'
            />
            <input
              placeholder='请输入密码'
              type='password'
              required
              minLength='6'
              value={123456}
              className='w-full shadow-lg  p-2 rounded-md outline-none border-1 text-gray-600 focus:ring-2 focus:ring-blue-400'
              ref={password}
            />
            <button className='w-full shadow-lg bg-blue-400 p-2   rounded-md text-white my-8'>
              登陆
            </button>
            <span className='text-gray-400 text-sm'>
              忘记密码？
            </span>
            <button
              onClick={handlerCreate}
              className='w-full  shadow-lg bg-pink-400  p-2 text-white rounded-md'
            >
              创建新账号
            </button>
          </form>
        </div>
      </div>
    </div>
  );
}
