import { MoreVert } from "@material-ui/icons";
import React, { useEffect, useContext } from "react";
import { useState } from "react";
import http from "../../serivce/API";
import { Link } from "react-router-dom";
import { format } from "timeago.js";
import { UserContext } from "../../context/UserContext";
export default function Post({ post }) {
  const [like, setLike] = useState(post.likes.length);
  const [isLiked, setIsLiked] = useState(false);
  const [visible, setVisible] = useState(false);

  const [user, setUser] = useState({});

  const { user: currentUser } = useContext(UserContext);

  useEffect(() => {
    const handler = () => {
      setVisible(false);
    };
    document.addEventListener("click", handler);
    const fetchUser = async () => {
      const res = await http.get(
        `/users/?userId=${post.userId}`
      );
      setUser(res.data);
    };
    fetchUser();
    return () => {
      document.removeEventListener("click", handler);
    };
  }, [post.userId, setVisible, visible]);

  const likeHandler = () => {
    setLike(isLiked ? like - 1 : like + 1);
    setIsLiked(!isLiked);
  };

  const handlerClick = (e) => {
    e.nativeEvent.stopImmediatePropagation();
    setVisible(!visible);
  };
  const handlerDelete = () => {
    const deletePost = async () => {
      if (post.userId === currentUser._id) {
        await http.delete("posts/" + post._id, {
          data: {
            userId: currentUser._id,
          },
        });
      } else {
        alert("你不能删除别人的博客");
      }
    };
    deletePost();
    window.location.reload();
  };

  const handlerFollow = () => {
    const followUser = async () => {
      if (currentUser?._id !== post.userId) {
        await http.put(`users/${post.userId}/follow`, {
          userId: currentUser?._id,
        });
        alert("关注成功");
      } else {
        alert("你不能关注自己");
      }
    };
    followUser();
  };

  const handlerUnfolllow = () => {
    const unFollowUser = async () => {
      if (currentUser._id !== post.userId) {
        await http.put(`users/${post.userId}/unfollow`, {
          userId: currentUser._id,
        });
        alert("取消关注成功");
        window.location.reload();
      } else {
        alert("你不能取消关注自己");
      }
    };
    unFollowUser();
  };
  return (
    <div className='flex flex-col relative'>
      <div
        className={`${
          visible ? "inline-flex" : "hidden"
        } flex-col  bg-white text-gray-600  w-40 text-center rounded-md space-y-2 cursor-pointer absolute top-20 right-0 z-20 shadow-2xl overflow-hidden dark:bg-gray-800 dark:text-gray-200`}
      >
        <span
          className='hover:bg-pink-400 hover:text-white p-2'
          onClick={handlerFollow}
        >
          关注
        </span>
        <span
          className='hover:bg-pink-400 hover:text-white p-2'
          onClick={handlerUnfolllow}
        >
          取消关注
        </span>
        <span
          className='hover:bg-pink-400 hover:text-white p-2'
          onClick={handlerDelete}
        >
          删除
        </span>
        <span
          className='hover:bg-pink-400 hover:text-white p-2'
          onClick={handlerDelete}
        >
          加入特别关注
        </span>
      </div>
      <div className='flex flex-col bg-white rounded-md shadow-lg text-gray-600 p-4 space-y-4 dark:bg-gray-800 dark:text-gray-200'>
        <div className='flex justify-between items-center'>
          <div className='flex items-center space-x-4'>
            <Link to={`/profile/${user.username}`}>
              <img
                className='w-102 h-10 object-cover rounded-full'
                src={
                  user.profilePicture ||
                  "https://fujix.oss-cn-shenzhen.aliyuncs.com/noAvatar.png"
                }
                alt=''
              />
            </Link>
            <span className=''>{user.username}</span>
            <span className=''>
              {format(post.createdAt, "zh_CN")}
            </span>
          </div>

          <div className=''>
            <MoreVert
              onClick={handlerClick}
              className='cursor-pointer'
            />
          </div>
        </div>
        <div className='flex flex-col space-y-4'>
          <span
            className=''
            dangerouslySetInnerHTML={{ __html: post.desc }}
          ></span>
          <img
            src={post.img}
            alt=''
            className='rounded-sm'
          />
        </div>
        {/* <div className='flex items-center justify-evenly'>
          <div className='flex  items-center cursor-pointer'>
            <span>
              <ThumbUpAlt />
            </span>

            <span className='pl-1'>{like}</span>
          </div>
          <div className=''>
            <span className=''>{post.comment} 评论</span>
          </div>
        </div> */}
      </div>
    </div>
  );
}
