import React from "react";
import { useRef } from "react";
import http from "../../serivce/API";
import { useHistory } from "react-router";
export default function Login() {
  const password = useRef();
  const username = useRef();
  const passwordAgain = useRef();
  const email = useRef();
  const history = useHistory();

  const handlerClick = (e) => {
    e.preventDefault();
    if (
      password.current.value === passwordAgain.current.value
    ) {
      const handlerRegister = async () => {
        try {
          await http.post("auth/register", {
            username: username.current.value,
            password: password.current.value,
            email: email.current.value,
          });
        } catch (error) {
          console.log(error);
        }
      };
      handlerRegister();
      history.push("/login");
    } else {
      alert("两次输入密码不同");
    }
  };

  const handlerLogin = () => {
    history.push("/login");
  };
  return (
    <div className='mx-auto bg-gradient-to-r from-purple-400 via-pink-500 text-white flex flex-col justify-center space-y-6 h-screen'>
      <div className='flex flex-col h-screen justify-center items-center space-y-10'>
        <div className='w-full  md:w-1/2 flex justify-center items-center flex-col space-y-4 '>
          <h2 className='text-6xl font-bold'>Fujix</h2>
          <span>用照片分享你的生活</span>
        </div>
        <div className='w-full md:w-2/3  lg:w-1/2  flex justify-center items-center '>
          <form
            onSubmit={handlerClick}
            className='w-full m-4  md:w-1/2 flex bg-white p-4 flex-col justify-center items-center space-y-4  rounded-md shadow-xl '
          >
            <input
              placeholder='用户名'
              ref={username}
              required
              className='w-full shadow-lg focus:ring-2 focus:ring-blue-400  p-2 rounded-md outline-none  focus:border-blue-400 border-1 text-gray-600'
            />
            <input
              placeholder='邮箱'
              ref={email}
              required
              className='w-full shadow-lg focus:ring-2 focus:ring-blue-400  p-2 rounded-md outline-none  focus:border-blue-400 border-1 text-gray-600'
              type='email'
            />
            <input
              placeholder='密码'
              ref={password}
              type='password'
              minLength='6'
              required
              className='w-full shadow-lg focus:ring-2 focus:ring-blue-400  p-2 rounded-md outline-none  focus:border-blue-400 border-1 text-gray-600'
            />
            <input
              placeholder='再次确认密码'
              ref={passwordAgain}
              type='password'
              required
              className='w-full shadow-lg focus:ring-2 focus:ring-blue-400  p-2 rounded-md outline-none  focus:border-blue-400 border-1 text-gray-600'
            />
            <button
              type='submit'
              className='w-full shadow-lg focus:ring-2 focus:ring-blue-400  bg-blue-400 p-2   rounded-md text-white my-8'
            >
              注册
            </button>

            <button
              onClick={handlerLogin}
              className='w-full shadow-lg focus:ring-2 focus:ring-blue-400 bg-pink-400  p-2 text-white rounded-md'
            >
              登陆
            </button>
          </form>
        </div>
      </div>
    </div>
  );
}
