import React, {
  useContext,
  useState,
  useEffect,
} from "react";
import { Formik, Field, Form } from "formik";

import Layout from "../../components/layout/Layout";
import { UserContext } from "../../context/UserContext";
import http from "../../serivce/API";
import { Settings } from "@material-ui/icons";
export default function Setting() {
  const { user: currentUser } = useContext(UserContext);
  const [file, setFile] = useState(null);
  const [user, setUser] = useState({});
  const { username, city, from, desc } = user;
  useEffect(() => {
    const fetchUser = async () => {
      const res = await http.get(
        `users/${currentUser._id}`
      );
      setUser(res.data);
    };
    fetchUser();
  }, [currentUser, setUser]);
  const handlerSubmit = (value) => {
    //更新完了。怎么重新获取用户数据？

    const newUser = {
      userId: currentUser?._id,
      ...value,
    };

    const updateUser = async () => {
      try {
        if (file) {
          const data = new FormData();
          const fileName = Date.now() + file.name;

          data.append("fileName", fileName);
          data.append("file", file);
          const { data: imgURL } = await http.post(
            "/upload",
            data
          );
          newUser.profilePicture = imgURL;
        }

        // 更新数据
        await http.put(`users/${currentUser._id}`, newUser);
        alert("保存成功");
      } catch (error) {
        console.log(error);
      }
    };
    updateUser();
  };

  const Basic = () => (
    <div className='mx-auto w-full bg-white text-gray-400  shadow-lg flex flex-col justify-center space-y-6 px-80 mb-10 py-20 dark:bg-gray-800 dark:text-gray-200 rounded-md'>
      <div className='space-y-4'>
        <h2 className='mb-4 flex items-center'>
          <Settings fontSize='large'></Settings>
          <span className='font-bold pl-4 text-2xl'>
            设置
          </span>
        </h2>
        <label htmlFor='file'>
          {file ? (
            <img
              alt=''
              className=''
              src={URL.createObjectURL(file)}
            />
          ) : (
            <img
              src={
                user.profilePicture ||
                "https://fujix.oss-cn-shenzhen.aliyuncs.com/noCover.png"
              }
              alt=''
              className='w-32 h-32 rounded-lg object-cover shadow-lg'
            />
          )}

          <input
            type='file'
            id='file'
            style={{ display: "none" }}
            accept='.png,.jpeg,.jpg'
            onChange={(e) => setFile(e.target.files[0])}
          />
        </label>
        <Formik
          initialValues={{ username, city, from, desc }}
          onSubmit={handlerSubmit}
        >
          <Form className='flex flex-col space-y-4'>
            <label htmlFor='username'>用户名字</label>
            <Field
              id='username'
              name='username'
              autoComplete='off'
              className='w-full shadow-lg p-2 rounded-md outline-none  border-1 text-gray-600 focus:ring-2 focus:ring-blue-400'
            />

            <label htmlFor='desc'>简介</label>
            <Field
              id='desc'
              name='desc'
              autoComplete='off'
              className='w-full shadow-lg p-2 rounded-md outline-none  border-1 text-gray-600 focus:ring-2 focus:ring-blue-400'
            />
            <label htmlFor='city'>城市</label>
            <Field
              id='city'
              name='city'
              autoComplete='off'
              className='w-full shadow-lg p-2 rounded-md outline-none  border-1 text-gray-600 focus:ring-2 focus:ring-blue-400'
            />

            <label htmlFor='from'>家乡</label>
            <Field
              id='from'
              name='from'
              autoComplete='off'
              className='w-full shadow-lg p-2 rounded-md outline-none  border-1 text-gray-600 focus:ring-2 focus:ring-blue-400'
            />
            <p></p>
            <button
              variant='contained'
              color='primary'
              type='submit'
              className='w-full shadow-lg bg-pink-400  mt-8 p-2 text-white rounded-md dark:bg-blue-400'
            >
              保存设置
            </button>
          </Form>
        </Formik>
      </div>
    </div>
  );
  return (
    <Layout>
      <Basic />
    </Layout>
  );
}
