// import "./topbar.css";
import { Search, Settings, Home } from "@material-ui/icons";
import React, { useContext } from "react";
import { Link, useHistory } from "react-router-dom";
import { UserContext } from "../../context/UserContext";
import Storage from "../../serivce/localStorage";
import { useFetchData } from "../../hooks/useFetchData";

export default function Topbar() {
  const { user: currentUser } = useContext(UserContext);
  const history = useHistory();

  const url = currentUser ? `users/${currentUser._id}` : "";
  const [user] = useFetchData(url);

  const handlerLogout = () => {
    history.push("/login");
    Storage.clear();
  };
  return (
    <div className='h-12 mx-auto max-w-7xl w-full bg-white flex items-center text-gray-700 dark:bg-gray-800 dark:text-white '>
      <div className='w-3/12'>
        <Link to='/home'>
          <span className='text-2xl  ml-6 cursor-pointer font-bold'>
            Fujix
          </span>
        </Link>
      </div>
      <div className='w-5/12'>
        <div className='w-full  flex items-center rounded-full h-8 bg-gray-100'>
          <Search className='ml-2' />
          <input
            type='text'
            placeholder=''
            className='outline-none pl-2 w-3/5 bg-gray-100'
          />
        </div>
      </div>
      <div className='w-4/12 flex items-center justify-evenly '>
        <Link to={user ? `/profile/${user.username}` : "/"}>
          <img
            src={
              user.profilePicture ||
              "https://fujix.oss-cn-shenzhen.aliyuncs.com/noAvatar.png"
            }
            className='rounded-full text-lg object-cover h-8 w-8'
            alt='avatart'
          />
        </Link>
        <Link to='/home'>
          <span>
            <Home />
          </span>
        </Link>
        <Link to='/setting'>
          <span>
            <Settings />
          </span>
        </Link>
        <span
          onClick={handlerLogout}
          className='cursor-pointer'
        >
          登出
        </span>
      </div>
    </div>
  );
}
