import React, {
  useRef,
  useState,
  useContext,
  useEffect,
} from "react";
import { PermMedia } from "@material-ui/icons/";

import http from "../../serivce/API";
import { UserContext } from "../../context/UserContext";

export default function Share({ username }) {
  const desc = useRef("");
  const [file, setFile] = useState(null);
  const { user } = useContext(UserContext);
  const [hide, setHide] = useState(true);

  const changeVisible = (e) => {
    e.nativeEvent.stopImmediatePropagation();
    setHide(!hide);
  };

  const format = (text) => {
    var replaceRegex = /(\n\r|\r\n|\r|\n)/g;

    text = text || "";

    return text.replace(replaceRegex, "<br/>");
  };

  const submitHandler = async (e) => {
    e.preventDefault();

    const newPost = {
      userId: user._id,
      desc: format(desc.current.value),
    };

    if (file) {
      const data = new FormData();
      const fileName = Date.now() + file.name;
      // 这边append 的顺序不对会导致服务器端娶不到 req.body
      data.append("fileName", fileName);
      data.append("file", file);
      try {
        const { data: imgURL } = await http.post(
          "/upload",
          data
        );

        newPost.img = imgURL;
      } catch (error) {}
    }

    try {
      await http.post("/posts", newPost);
      window.location.reload();
    } catch (error) {}
  };

  useEffect(() => {
    const handler = () => {
      setHide(true);
    };
    document.addEventListener("click", handler);

    const submitHandler = async () => {
      const newPost = {
        userId: user._id,
        desc: format(desc.current.value),
      };

      if (file) {
        const data = new FormData();
        const fileName = Date.now() + file.name;
        // 这边append 的顺序不对会导致服务器端娶不到 req.body
        data.append("fileName", fileName);
        data.append("file", file);
        newPost.img = fileName;
        try {
          await http.post("/upload", data);
        } catch (error) {}
      }

      try {
        await http.post("/posts", newPost);
        window.location.reload();
      } catch (error) {
        console.log(error);
      }
    };
    const keydownHandler = (e) => {
      if (e.keyCode === 13 && e.ctrlKey) {
        submitHandler();
      }
    };

    document.addEventListener("keydown", keydownHandler);

    return () => {
      document.removeEventListener(
        "keydown",
        keydownHandler
      );
      document.removeEventListener("click", handler);
    };
  }, [file, user]);

  const ShareComponent = () => (
    <div className='w-full p-4 bg-white shadow-lg rounded-md  dark:bg-gray-800 dark:text-gray-200'>
      <div className=''>
        <img src='' alt='' />
        <textarea
          type='text'
          placeholder='有什么有趣的事情分享给大家?'
          className='w-full resize-none outline-none dark:bg-gray-800 dark:text-gray-200'
          ref={desc}
        />
      </div>
      <hr className='my-4' />
      {file && (
        <div className='flex justify-center items-center'>
          <img
            alt=''
            className='rounded-sm max-h-96 shadow-lg'
            src={URL.createObjectURL(file)}
          ></img>
        </div>
      )}
      <form
        className='flex justify-between'
        onSubmit={submitHandler}
      >
        <div className=''>
          <label htmlFor='file' className=''>
            <PermMedia htmlColor='tomato' className='' />

            <input
              type='file'
              id='file'
              style={{ display: "none" }}
              accept='.png,.jpeg,.jpg'
              onChange={(e) => setFile(e.target.files[0])}
            />
          </label>
        </div>

        <button className='' type='submit'>
          发送
        </button>
      </form>
    </div>
  );

  // 条件渲染Share 组件
  if (username === undefined) {
    return <ShareComponent />;
  } else if (username === user?.username) {
    return <ShareComponent />;
  } else {
    return null;
  }
}
