import React, {
  useState,
  useEffect,
  useContext,
} from "react";
import http from "../../serivce/API";
import dayjs from "dayjs";
import { UserContext } from "../../context/UserContext";
import { Chrono } from "react-chrono";
export default function Timeline() {
  const [posts, setPosts] = useState([]);
  const { user } = useContext(UserContext);

  useEffect(() => {
    const fetchPosts = async () => {
      const res = await http.get(
        `posts/profile/${user.username}`
      );

      const filterPosts = res.data?.sort((p1, p2) => {
        return (
          new Date(p2.createdAt) - new Date(p1.createdAt)
        );
      });
      let displayPosts = [];

      filterPosts.forEach((item) => {
        if (item.desc !== "") {
          let arr = {};
          arr.title = dayjs(item.createdAt).format(
            "DD/MM/YYYY"
          );
          arr.cardDetailedText = item.desc;
          displayPosts.push(arr);
        }
      });


      setPosts(displayPosts);
    };

    fetchPosts();
  }, [user]);

  return (
    <div className='dark:bg-gray-800 dark:text-gray-200  p-4 pb-24 mb-20 overflow-hidden rounded-md shadow-lg bg-white'>
      <div className='text-center my-4'>
        <div></div>
        {posts && (
          <Chrono
            slideShow
            items={posts}
            mode='VERTICAL_ALTERNATING'
            hideControls
            useReadMore={false}
            allowDynamicUpdate={true}
            scrollable={true}
          />
        )}
      </div>
    </div>
  );
}
