import { useEffect, useState } from "react";
import API from "../serivce/API";
export const useFetchData = (URL) => {
  const [user, setUser] = useState({});

  useEffect(() => {
    const fetchUser = async () => {
      const res = await API.get(URL);
      setUser(res.data);
    };
    fetchUser();
  }, [URL]);
  return [user];
};
