import { useContext, useState, useEffect } from "react";
import Online from "../online/Online";
import { UserContext } from "../../context/UserContext";
import http from "../../serivce/API";
import { Link } from "react-router-dom";
import Userbox from "../userbox/Userbox";
export default function Rightbar({ user }) {
  const [friends, setFriends] = useState([]);
  const { user: currentUser } = useContext(UserContext);
  const [circle, setCircle] = useState([]);

  useEffect(() => {
    const fetchUsers = async () => {
      const res = await http.get(
        `users/friends/${currentUser?._id}`
      );

      setFriends(res?.data);
    };
    fetchUsers();
  }, [currentUser]);

  useEffect(() => {
    const fetchCircle = async () => {
      const res = await http.get(
        `users/circle/${currentUser?._id}`
      );
      setCircle(res?.data);
    };
    fetchCircle();
  }, [currentUser]);

  const ProfileRightbar = () => {
    return (
      <div className='flex flex-col space-y-6 ml-4'>
        {/* BUG */}
        <Userbox />
        <div className='bg-white p-4 text-gray-600 flex flex-col space-y-4 shadow-lg rounded-md dark:bg-gray-800 dark:text-gray-100'>
          <h4 className=''>我的关注</h4>
          <ul className='flex flex-col space-y-4'>
            {friends &&
              friends.map((u) => (
                <Online key={u?._id} user={u} />
              ))}
          </ul>
        </div>
      </div>
    );
  };

  const ProfileRightbarOption = () => {
    return (
      <>
        <div className='rounded-md shadow-xl p-4 space-y-4 dark:bg-gray-800 dark:text-gray-200'>
          <div>简介</div>
          <div className=''>
            <div className=''>
              <span className=''>居住地:</span>
              <span className=''>{user.city}</span>
            </div>
            <div className=''>
              <span className=''>家乡:</span>
              <span className=''>{user.from}</span>
            </div>
            <div className=''>
              <span className=''>职业:</span>
              <span className=''>{user.relationship}</span>
            </div>
          </div>
        </div>

        <div className='rounded-md shadow-xl p-4 space-y-4 dark:bg-gray-800 dark:text-gray-200'>
          <h4 className=''>好友圈</h4>
          {circle &&
            circle.map((u) => {
              return (
                <Link
                  to={`/profile/${u.username}`}
                  key={u?._id}
                  className='block'
                >
                  <div className='flex items-center justify-between'>
                    <img
                      src={
                        u.profilePicture
                          ? u.profilePicture
                          : "https://fujix.oss-cn-shenzhen.aliyuncs.com/noCover.png"
                      }
                      alt=''
                      className='w-16 h-16 object-cover rounded-full'
                    />
                    <span className=''>{u?.username}</span>
                  </div>
                </Link>
              );
            })}
        </div>
      </>
    );
  };
  return (
    <>
      {user ? (
        <ProfileRightbarOption />
      ) : (
        <ProfileRightbar />
      )}
    </>
  );
}
