import React from "react";
import { Route, Redirect } from "react-router-dom";

export default function protectedRoute({
  isAuth,
  component: Component,
  ...rest
}) {
  return (
    <Route
      {...rest}
      render={(routeProps) => {
        if (isAuth) {
          return <Component {...routeProps} />;
        } else {
          return <Redirect to='/login' />;
        }
      }}
    ></Route>
  );
}
