import { useContext } from "react";
import React from "react";
import { UserContext } from "../../../context/UserContext";
import { useFetchPosts } from "../../../hooks/useFetchPostsPhoto";
export default function Photos() {
  const { user } = useContext(UserContext);

  let url = `posts/portfolio/${user?._id}`;
  const posts = useFetchPosts(url);
  return (
    <div className='flex flex-wrap  bg-white p-4 rounded-md mb-10 dark:bg-gray-800'>
      {posts.map((p) => {
        return (
          <img
            key={p._id}
            src={p.img}
            alt=''
            className='w-1/3 max-h-80 object-cover opacity-95 p-2 scale-100 cursor-pointer transform  transition duration-500 ease-in-out  hover:opacity-100  hover:scale-105'
          ></img>
        );
      })}
    </div>
  );
}
